img{display: none;}
.enemy{
position: absolute;
inset: 0;margin: auto;
width: 100%;
isolation: isolate;
// background-color: red;

& img {
  display: initial;
  position: absolute;
  width: 100%; height: auto;
  inset: 0;margin: auto;
  object-fit: contain;

  }
}

.tiger{
  width: 95%;
}




// enem boxes
.enemySelector , .friendlySelector {
  position: absolute;
  inset: 0;margin: auto;
  width: 100%;height: 100%;
  border: 5px dotted rgb(255, 217, 0);
  transition: transform 300ms ease-out;
  transform: scale(0);
}
.enemySelector.show ,.friendlySelector.show {
  transform: scale(1.2);
}

.enemy-grid{
  position: absolute;
  right: 0;top: 20px;
  width: 70%;height: 60%;
  border: 3px solid red;

  transform-origin: bottom right;

  display: grid;
  grid-template-columns: repeat(4,1fr);
  grid-template-rows: repeat(2,1fr);
}
.enemy-box{
  position: relative;
  width: 100%;height: 100%;background-color: rgba(255, 255, 255, 0.363);
  border: 3px solid black;
  display: flex;
  // justify-content: center;
  // align-items: center;
}

.enemy-box[data-enemy-box1=""]{
  grid-column: 4/5;
  grid-row:2/3 ;
}
.enemy-box[data-enemy-box2=""]{
  grid-column: 3/4;
  grid-row:2/3 ;
}
.enemy-box[data-enemy-box3=""]{
  grid-column: 2/3;
  grid-row:2/3 ;
}
.enemy-box[data-enemy-box4=""]{
  grid-column: 1/2;
  grid-row:2/3 ;
}
.enemy-box[data-enemy-box5=""]{
  grid-column: 4/5;
  grid-row:1/2 ;
}
.enemy-box[data-enemy-box6=""]{
  grid-column: 3/4;
  grid-row:1/2 ;
}
.enemy-box[data-enemy-box7=""]{
  grid-column: 2/3;
  grid-row:1/2 ;
}
.enemy-box[data-enemy-box8=""]{
  grid-column: 1/2;
  grid-row:1/2 ;
}

@media (max-width: 1000px) {
.enemy-grid{
  transform: scale(.85);
}
}